.researchScreen {
    width: 100%;
    color: white;
}

.researchCont{
    min-height: 100vh;
    padding-top: 11%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    .servicesBox{
        margin: 30px;
        width: 400px;
        height: 400px;
    }
}


@include phone{
    .researchCont{
        padding-top: 100px;
        flex-direction: column;
    }
    .researchCont > .servicesBox{
        width: 85%;
    }
}

.researchCont > .servicesBox > h2{
    max-width: 85%;
}

.researchCont > .servicesBox > h3{
    margin-top: 0;
}

.bioMedires{
    min-height: 100vh;
    background: #494947;
}

.logos{
    @include flexCenter();
}

.logos > img{
    width: 17%;
    object-fit: cover;
    margin: 10px;
    @include shadow();
}

.researchGarants{
    padding: 30px;
    @include phone{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 !important;
        padding-top: 20px !important;
    }
}

.opvai{
    padding: 0 80px 0 0;
    box-sizing: border-box;
}
