.servicesScreen {
    width: 100%;
    color: white;
    position: relative;
}

.services-svg{
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
    opacity: 1;
    &.pre{
        bottom: -60vh;
        opacity: 0;
    }
}




.servicesCont {
    min-height: 100vh;
    padding-top: 13%;
    box-sizing: border-box;
    width: 100%;
    @include flexCenter();
    align-items: flex-start;
    /*@include centeredBg('abc2.jpg');*/
    flex-wrap: wrap;
    @include phone{
        padding-top: 26%;
    }
}


.servicesBox {
    cursor: pointer;
    display: flex;
    position: relative;
    flex-direction: column;
    align-items: center;
    margin: 10px;
    background: white;
    color: black;
    height: 380px;
    border-radius: 4px;
    width: 300px;
    transition: $short;
    @include shadow();
    &:hover{
        transform: scale(1.1);
        div {
            background: $lightblue;
        }
    }
    @media(max-width:600px){
        width: 85%;
        margin-bottom: 50px;
    }
    img {
        margin-top: 10px;
        display: block;
        width: 70%;
        max-width: 200px;
        object-fit: cover;
    }
    h2 {
        max-width: 50%;
        margin-top: 5px;
        text-align: center;
        color: $lightblue;
    }
    div {
        transition: $short;
        margin-top: auto;
        position: absolute;
        bottom: 0;
        height: 40px;
        background: $darkblue;
        width: 100%;
        border-bottom-left-radius: 4px;
        border-bottom-right-radius: 4px;
        text-align: center;
        color: white;
        font-size: 20px;
        line-height: 36px;
    }
    h3{
        font-size: 15px;
        text-align: center;
        margin-top: 0;
        color: black !important;
    }
}