.productsScreen {
    width: 100%;
    color: white;
    position: relative;
}

.productsCont {
    min-height: 100vh;
    padding-top: 13%;
    box-sizing: border-box;
    width: 100%;
    @include flexCenter();
    align-items: flex-start;
    flex-wrap: wrap;
    @include phone{
        padding-top: 26%;
    }
}

.products-svg{
    width: 120vw;
    position: absolute;
    bottom: -20vh;
    left: 0;
    opacity: 1;
    &.pre{
        left: -30vw;
        opacity: 0;
    }
}