.benefitsHeading{
    width: 250px;
    position: relative;
    img{
        position: absolute;
        width: 400px;
        object-fit: contain;
        z-index: 2;
        top: 0;
        left: 0;
    }
}


.benefitsHeading h2{
    margin: 0;
     z-index: 20;
    text-align: center;
    position: relative;
    margin-top: 30px;
    font-size: 30px;
    border-bottom-right-radius: 30px;
    border-top-right-radius: 30px;
    color:white;
    padding: 50px;
    width: 200px;
}

.benefitsGraphic{
    position: relative;
    margin-top: 50px;
    width: 400px;
}

.benefitsGraphic img{
    object-fit: contain;
     position: absolute;
}

.benefitsGraphic> img:nth-child(1){
    top:10px; 
    width: 350px;
}

.benefitsGraphic> img:nth-child(2){
    top:90px;
    left: -40px;
    width: 340px;
}

.benefitsCont{
    @include centeredBg('abc2.jpg'); 
    position: relative;
    height: 100vh;
}

.benefitsBoxCont{
    margin-top: 50px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    
}

.benefitsBox{
    width: 34%;
    z-index: 20;
    position: relative;
    height: fit-content;
    min-height: 200px;
    padding: 30px;
    display: flex;
    margin: 0 40px;
    border-radius: 20px;
    color: black;
        ion-icon{
            color: white;
            min-height: 40px;
            min-width: 40px;
            border-radius: 50%;
            background: $darkblue;
            padding: 20px;
            margin-right: 20px;
        }
    p{
        line-height: 25px;
    }
}

.benefitsBoxHeader{
    display: flex;
}

.benefitsBoxHeader img{
    width: 80px;
    display: block;
    object-fit: contain;
}

.benefitsBoxHeader h2{
    margin-left: 20px;
    color: $darkblue;
}

