body{
  @include centeredBg('abc2.jpg')  
}

.homeScreen {
    width: 100%;
    min-height: 100vh;
    position: relative;
    display: flex;
    div{
        flex: 1;
    }
    .icon-cont{
        @include flexCenter()
    }

}

.home-svg{
    position: fixed;
    width: 100vw;
    right: 0;
    bottom: 0;
    opacity: 1;
    &.pre{
        bottom: -60vh;
        opacity: 0;
    }
}

.main-heading{
    padding-top: 180px;
    margin-left: 140px;
    font-size: 40px;
    text-transform: uppercase;
    .what-we-do{
        font-size: 22px;
        max-width: 500px;
        margin-top: 20px;
        line-height: 32px;
        display: block;
        text-transform: none;
        color: rgba(0,0,0,0.4);
    }
}

    .home-icon{
        object-fit: contain;
        width: 80%;
        max-height: 600px;
        margin-top: 40px;
        height: 80%;
    }


.moreInfo {
    font-size: 20px;
    margin-left: 140px;
    margin-top: 30px;
    padding: 12px 30px;
    border-radius: 30px;
    outline: none;
    border: none;
    position: relative;
    background: $darkblue;
    color: white;
    cursor: pointer;
    transition: $short;
    @include shadow();
}

@include phone{
    .main-heading {
        font-size: 50px;
        padding-top: 170px;
        padding-left: 50px;
    }
    .moreInfo{
        margin-left: 50px;
    }
}

.slideHvr:hover::before {
    opacity: 1;
    width: 100%;
}

.slideHvr::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;
    z-index: 1;
    border-radius: 29px;
    opacity: 0;
    background-color: rgba(255,255,255,0.2);
    transition: $short;
}